import React, { useEffect, useState } from 'react';
import { BarChart } from '../../../components/charts/barchart/Barchart';
import { AirlineTable } from '../../../components/tables/airline_table/AirlineTable';
import { Tabs } from '../../../components/tabs/Tabs';
import { fetchSinToken } from '../../../helpers/fetch';


export const Question2 = () => {

    const get_q2_salida = 'consulta2?viaje=salida';
    const get_q2_llegada = 'consulta2?viaje=llegada';
    // const get_q2_salida = 'q2_salida.json';
    // const get_q2_llegada = 'q2_llegada.json';

    const [dataq2_salida,setDataq2_salida] = useState([]);
    const [dataq2_llegada,setDataq2_llegada] = useState([]);
    const [q2_salidaTotal,setQ2_salidaTotal] = useState([0]);
    const [q2_llegadaTotal,setQ2_llegadaTotal] = useState([0]);
    const [q2_labels,setQ2_labels] = useState(['aero 1', 'aero 2', 'aero 3', 'aero 4', 'aero 5', 'aero 6', 'aero 7','aero 8','aero 9','aero 10','aero 11','aero 12','aero 13','aero 14','aero 15','aero 16','aero 17','aero 18','aero 19','aero 20']);
    const [q2_salidaCounter,setQ2_salidaCounter] = useState([75, 59, 80, 81, 56,75, 59, 80, 81, 56,75, 59, 80, 81, 56,75, 59, 80, 81, 56]);
    const [q2_llegadaCounter,setQ2_llegadaCounter] = useState([65, 59, 80, 81, 56,75, 59, 80, 81, 56,75, 59, 80, 81, 56,75, 59, 80, 81, 56]);

    const [isVisibleTab1Q2,setIsVisibleTab1Q2] = useState(0);

    const q2Data = {
        labels: q2_labels,
        datasets: [
            {
                label: isVisibleTab1Q2 === 0 ? 'Llegada' : 'Salida',
                backgroundColor: isVisibleTab1Q2 === 0 ? '#1d53be' : '#99bf22',
                data: isVisibleTab1Q2 === 0 ? q2_llegadaCounter : q2_salidaCounter 
            },
            // {
            //     label: 'Salida',
            //     backgroundColor: '#1d53be',
            //     data: q2_salidaCounter  //salida
            // }
        ]
    };

    const getLightTheme = () => {
        let basicOptions = {
            maintainAspectRatio: false,
            aspectRatio: .8,
            plugins: {
                legend: {
                    labels: {
                        color: '#495057'
                    }
                }
            },
            scales: {
                x: {
                    ticks: {
                        color: '#495057'
                    },
                    grid: {
                        color: '#ebedef'
                    }
                },
                y: {
                    ticks: {
                        color: '#495057'
                    },
                    grid: {
                        color: '#ebedef'
                    }
                }
            }
        };
    
        let horizontalOptions = {
            indexAxis: 'y',
            maintainAspectRatio: false,
            aspectRatio: .8,
            plugins: {
                legend: {
                    labels: {
                        color: '#495057'
                    }
                }
            },
            scales: {
                x: {
                    ticks: {
                        color: '#495057'
                    },
                    grid: {
                        color: '#ebedef'
                    }
                },
                y: {
                    ticks: {
                        color: '#495057'
                    },
                    grid: {
                        color: '#ebedef'
                    }
                }
            }
        };
    
        return {
            basicOptions,
            horizontalOptions,
        }
    }

    const { horizontalOptions} = getLightTheme();

    const sortJSON = (data, key, orden) => {
        return data.sort(function (a, b) {
            var x = a[key],
            y = b[key];
    
            if (orden === 'asc') {
                return ((x < y) ? -1 : ((x > y) ? 1 : 0));
            }
    
            if (orden === 'desc') {
                return ((x > y) ? -1 : ((x < y) ? 1 : 0));
            }
        });
    }

    const filtrarPorCampo = (array,field) => {
        return array.map((item) => item[field]);
    }

    const obtenerTotal = (array,field) => {
        let total = array.reduce((sum, value) => (typeof value[field] == "number" ? sum + value[field] : sum), 0);
        console.log(total);
        return total; 
    }

    const getDatosQ2Llegada = async(url, setData) => {
        try {
          const res = await fetchSinToken(`${url}`); //
          const body = await res.json();
          await setData(body['data']);
          let ordenado = sortJSON(body['data'],'retraso_llegada','desc');
          setQ2_labels(filtrarPorCampo(ordenado,'aerolinea_code').slice(0,10));
          setQ2_llegadaTotal(obtenerTotal(ordenado,'retraso_llegada'));
          setQ2_llegadaCounter(filtrarPorCampo(ordenado,'retraso_llegada').slice(0,10));
        } catch (error) {
          console.log(error)
        }
    }
    const getDatosQ2Salida = async(url, setData) => {
        try {
          const res = await fetchSinToken(`${url}`); //
          const body = await res.json();
          await setData(body['data']);
          let ordenado = sortJSON(body['data'],'retraso_salida','desc');
          setQ2_labels(filtrarPorCampo(ordenado,'aerolinea_code').slice(0,10));
          setQ2_salidaTotal(obtenerTotal(ordenado,'retraso_salida'));
          setQ2_salidaCounter(filtrarPorCampo(ordenado,'retraso_salida').slice(0,10));
        } catch (error) {
          console.log(error)
        }
    }

    function sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }
    useEffect(()=>{
        async function fetchData() {
            for (let i = 0; i < 50; i++) {
                // getDatosQ1Salida(get_q1_salida,setDataq1_salida);
                // getDatosQ1Llegada(get_q1_llegada,setDataq1_llegada);
                getDatosQ2Salida(get_q2_salida,setDataq2_salida);
                getDatosQ2Llegada(get_q2_llegada,setDataq2_llegada);
                console.log('hace peticion');
                await sleep(10000);          
                console.log('pasa 10 segundos');
            }
        }
        fetchData();
    },[]); // eslint-disable-line react-hooks/exhaustive-deps

    useEffect(()=>{
        let ordenado = sortJSON(isVisibleTab1Q2===0 ? dataq2_llegada:  dataq2_salida,isVisibleTab1Q2===0 ? 'retraso_llegada' : 'retraso_salida','desc');
        // setQ2_salidaCounter(filtrarPorCampo(ordenado,'retraso_salida').slice(0,10));
        setQ2_labels(filtrarPorCampo(ordenado,'aerolinea_code').slice(0,10));

    },[isVisibleTab1Q2]);// eslint-disable-line react-hooks/exhaustive-deps

    return <div className="row_2">
                <div className="cards_row2">
                    <div className="cards_columns2">
                        <div className="indicator indicator1">
                        <span className="title">LLEGADA</span>
                        <span className="counter">{q2_llegadaTotal}</span>
                        </div>
                        <div className="indicator indicator2">
                        <span className="title">SALIDA</span>
                        <span className="counter">{q2_salidaTotal}</span>
                        </div>
                    </div>

                    <BarChart
                        data={q2Data}
                        options={horizontalOptions}
                    />
                    </div>

                    <div id="2" className="tabs_container">
                    <Tabs
                        title="Nmero de retrasos por aerolnea"
                        header1="Llegada"
                        header2="Salida"
                        isVisibleTab1={isVisibleTab1Q2}
                        setIsVisibleTab1={setIsVisibleTab1Q2}
                    >
                        <AirlineTable
                            title="Llegada"
                            data={dataq2_llegada}
                            counter={"retraso_llegada"}
                        />
                        <AirlineTable
                            title="Salida"
                            data={dataq2_salida}
                            counter={"retraso_salida"}

                        />
                    </Tabs>
                    </div>
                </div>
};
