
import React from 'react';
import { Chart } from 'primereact/chart';

export const PieChart = ({data,labels}) => {
    const chartData = {
        labels: labels,
        datasets: [
            {
                data: data,
                backgroundColor: [
                    "#284277",
                    "#006bbd",
                    "#81c8ee",
                    "#ede8e4",
                    "#c1afa8"
                ],
                hoverBackgroundColor: [
                    "#90B4FC",
                    "#90B4FC",
                    "#90B4FC",
                    "#90B4FC",
                    "#90B4FC"
                ]
            }
        ]
    };

    const lightOptions = {
        plugins: {
            legend: {
                labels: {
                    color: '#495057'
                }
            }
        }
    };

    return (
        <div className="flex justify-content-center">
            <Chart 
                type="pie" 
                data={chartData} 
                options={lightOptions} 
                style={{ position: 'relative', width: '90%'}} />
        </div>
    )
}
                 