import React, { useEffect, useState } from 'react';
import { BarChart } from '../../../components/charts/barchart/Barchart';
import { RouteTable } from '../../../components/tables/route_table/RouteTable';
import { Tabs } from '../../../components/tabs/Tabs';
import { fetchSinToken } from '../../../helpers/fetch';

export const Question1 = () => {

    const get_q1_salida = 'consulta1?viaje=salida';
    const get_q1_llegada = 'consulta1?viaje=llegada';
    // const get_q1_salida = 'respuestas.json';
    const [dataq1_salida,setDataq1_salida] = useState([]);
    const [dataq1_llegada,setDataq1_llegada] = useState([]);
    const [q1_salidaTotal,setQ1_salidaTotal] = useState([0]);
    const [q1_llegadaTotal,setQ1_llegadaTotal] = useState([0]);
    const [q1_labels,setQ1_labels] = useState([]);
    const [q1_salidaCounter,setQ1_salidaCounter] = useState([]);
    const [q1_llegadaCounter,setQ1_llegadaCounter] = useState([]);

    const [isVisibleTab1Q1,setIsVisibleTab1Q1] = useState(0);

    const q1Data = {
        labels: q1_labels,
        datasets: [
            {
                label: isVisibleTab1Q1 === 0 ? 'Llegada' : 'Salida',
                backgroundColor: isVisibleTab1Q1 === 0 ? '#1d53be' : '#99bf22',
                data: isVisibleTab1Q1 === 0 ? q1_llegadaCounter : q1_salidaCounter
            }
        ]
    };

    const getLightTheme = () => {
        let basicOptions = {
            maintainAspectRatio: false,
            aspectRatio: .8,
            plugins: {
                legend: {
                    labels: {
                        color: '#495057'
                    }
                }
            },
            scales: {
                x: {
                    ticks: {
                        color: '#495057'
                    },
                    grid: {
                        color: '#ebedef'
                    }
                },
                y: {
                    ticks: {
                        color: '#495057'
                    },
                    grid: {
                        color: '#ebedef'
                    }
                }
            }
        };
    
        let horizontalOptions = {
            indexAxis: 'y',
            maintainAspectRatio: false,
            aspectRatio: .8,
            plugins: {
                legend: {
                    labels: {
                        color: '#495057'
                    }
                }
            },
            scales: {
                x: {
                    ticks: {
                        color: '#495057'
                    },
                    grid: {
                        color: '#ebedef'
                    }
                },
                y: {
                    ticks: {
                        color: '#495057'
                    },
                    grid: {
                        color: '#ebedef'
                    }
                }
            }
        };
    
        return {
            basicOptions,
            horizontalOptions,
        }
    }

    const { basicOptions } = getLightTheme();

    const sortJSON = (data, key, orden) => {
        return data.sort(function (a, b) {
            var x = a[key],
            y = b[key];
    
            if (orden === 'asc') {
                return ((x < y) ? -1 : ((x > y) ? 1 : 0));
            }
    
            if (orden === 'desc') {
                return ((x > y) ? -1 : ((x < y) ? 1 : 0));
            }
        });
    }

    const filtrarPorCampo = (array,field) => {
        return array.map((item) => item[field]);
    }

    const obtenerTotal = (array,field) => {
        let total = array.reduce((sum, value) => (typeof value[field] == "number" ? sum + value[field] : sum), 0);
        console.log(total);
        return total; 
    }

    const getDatosQ1Salida = async(url, setData) => {
        try {
          const res = await fetchSinToken(`${url}`); //
          const body = await res.json();
          setData(body['data']); 
          let ordenado = sortJSON(body['data'],'retraso_salida','desc');
          setQ1_labels(filtrarPorCampo(ordenado,'origen').slice(0,10));
          setQ1_salidaTotal(obtenerTotal(body['data'],'retraso_salida'));
          setQ1_salidaCounter(filtrarPorCampo(ordenado,'retraso_salida').slice(0,10));
        } catch (error) {
          console.log(error)
        }
    }
    const getDatosQ1Llegada = async(url, setData) => {
        try {
          const res = await fetchSinToken(`${url}`); //
          const body = await res.json();
          await setData(body['data']);
          let ordenado = sortJSON(body['data'],'retraso_llegada','desc');
          setQ1_labels(filtrarPorCampo(ordenado,'origen').slice(0,10));
          setQ1_llegadaTotal(obtenerTotal(body['data'],'retraso_llegada'));
          setQ1_llegadaCounter(filtrarPorCampo(ordenado,'retraso_llegada').slice(0,10));
        } catch (error) {
          console.log(error)
        }
    }

    function sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }

    useEffect(()=>{
        async function fetchData() {
            for (let i = 0; i < 50; i++) {
                getDatosQ1Salida(get_q1_salida,setDataq1_salida);
                getDatosQ1Llegada(get_q1_llegada,setDataq1_llegada);
                await sleep(10000);          
                console.log('pasa 10 segundos');
            }
        }
        fetchData();
    },[]); // eslint-disable-line react-hooks/exhaustive-deps

    useEffect(()=>{
        let ordenado = sortJSON(isVisibleTab1Q1 === 0 ? dataq1_llegada : dataq1_salida, isVisibleTab1Q1 === 0 ? 'retraso_llegada' : 'retraso_salida','desc');        
        setQ1_labels(filtrarPorCampo(ordenado,'origen').slice(0,10));
    },[isVisibleTab1Q1]);// eslint-disable-line react-hooks/exhaustive-deps

  return (
    <div className="row_2">
        <div id="1" className="tabs_container">
        <Tabs
            title="Nmero de retrasos por ruta"
            header1="Llegada"
            header2="Salida"
            isVisibleTab1={isVisibleTab1Q1}
            setIsVisibleTab1={setIsVisibleTab1Q1}
        >
            <div className="tab">
                <RouteTable
                    title="Llegada"
                    data={dataq1_llegada}
                    counter={"retraso_llegada"} //ultima columna
                />
            </div>
            <div className="tab">
                <RouteTable
                    title="Salida"
                    data={dataq1_salida}
                    counter={"retraso_salida"}  //ultima columna
                />
            </div>
        </Tabs>
        </div>

        <div className="cards_row2">
        <div className="cards_columns2">
            <div className="indicator indicator1">
            <span className="title">LLEGADA</span>
            <span className="counter">{q1_llegadaTotal}</span>
            </div>
            <div className="indicator indicator2">
            <span className="title">SALIDA</span>
            <span className="counter">{q1_salidaTotal}</span>
            </div>
        </div>
        <BarChart
            data={q1Data}
            options={basicOptions}
        />
        </div>
    </div>
  );
};
