import React, { useState } from 'react';
import 'primeicons/primeicons.css';
import 'primereact/resources/themes/lara-light-indigo/theme.css';
import 'primereact/resources/primereact.css';
import 'primeflex/primeflex.css';

import { TabView, TabPanel } from 'primereact/tabview';

import './tabs.css';
// import {ImArrowDownRight,ImArrowUpRight} from 'react-icons/im'

export const Tabs = ({title,children,header1,header2,setIsVisibleTab1}) => {

    const [activeIndex1, setActiveIndex1] = useState(0);

    const changeTab = (e) => {
        setActiveIndex1(e.index);
        setIsVisibleTab1(e.index);
    }

    return (
        <>
            <div className="card">
            <h4>{title}</h4>
                <TabView activeIndex={activeIndex1} onTabChange={(e) => changeTab(e)}>
                    <TabPanel header={header1}>
                        {children[0]}
                    </TabPanel>
                    <TabPanel header={header2}>
                        {children[1]}
                    </TabPanel>

                </TabView>
            </div>             
        </>
    )
};
