import React, { useEffect, useState } from 'react';
import { WeatherTable } from '../../../components/tables/weather_table/WeatherTable';
import { Tabs } from '../../../components/tabs/Tabs';
import { PieChart } from '../../../components/charts/piechart/Piechart';
import { fetchSinToken } from '../../../helpers/fetch';

export const Question3 = () => {

    const get_q3_aerolinea = 'consulta3?viaje=aerolinea';
    const get_q3_clima = 'consulta3?viaje=clima';

    const [dataq3_aerolinea,setDataq3_aerolinea] = useState([]);
    const [dataq3_clima,setDataq3_clima] = useState([]);
    const [q3_aerolineaTotal,setQ3_aerolineaTotal] = useState([0]);
    const [q3_climaTotal,setQ3_climaTotal] = useState([0]);
    const [q3_aerolineaCounter,setQ2_aerolineaCounter] = useState([]);
    const [q3_climaCounter,setQ2_climaCounter] = useState([]);

    const [isVisibleTab1Q3,setIsVisibleTab1Q3] = useState(0);

    const sortJSON = (data, key, orden) => {
        return data.sort(function (a, b) {
            var x = a[key],
            y = b[key];
    
            if (orden === 'asc') {
                return ((x < y) ? -1 : ((x > y) ? 1 : 0));
            }
    
            if (orden === 'desc') {
                return ((x > y) ? -1 : ((x < y) ? 1 : 0));
            }
        });
    }

    const obtenerTotal = (array,field) => {
        let total = array.reduce((sum, value) => (typeof value[field] == "number" ? sum + value[field] : sum), 0);
        console.log(total);
        return total; 
    }

    const getDatosQ3Aerolinea = async(url, setData) => {
        try {
          const res = await fetchSinToken(`${url}`); //
          const body = await res.json();
          await setData(body['data']);
          let ordenado = sortJSON(body['data'],'retraso_aerolinea','desc');
          setQ3_aerolineaTotal(obtenerTotal(ordenado,'retraso_aerolinea'));

        } catch (error) {
          console.log(error)
        }
    }
    const getDatosQ3Clima = async(url, setData) => {
        try {
          const res = await fetchSinToken(`${url}`); //
          const body = await res.json();
          await setData(body['data']);
          let ordenado = sortJSON(body['data'],'retraso_clima','desc');
          setQ3_climaTotal(obtenerTotal(ordenado,'retraso_clima'));
        } catch (error) {
          console.log(error)
        }
    }

    function sleep(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }
    useEffect(()=>{
        async function fetchData() {
            for (let i = 0; i < 50; i++) {
                getDatosQ3Aerolinea(get_q3_aerolinea,setDataq3_aerolinea);
                getDatosQ3Clima(get_q3_clima,setDataq3_clima);
                console.log('hace peticion');
                await sleep(10000);          
                console.log('pasa 10 segundos');
            }
        }
        fetchData();
    },[]); // eslint-disable-line react-hooks/exhaustive-deps


  return (
    <div className="row_2">
        <div id="3" className="tabs_container">
        <Tabs
            title="Nmero de retrasos por aerolnea y por condiciones climticas, por ruta"
            header1="Retrasos por Aerolinea"
            header2="Retrasos por Clima"
            isVisibleTab1={isVisibleTab1Q3}
            setIsVisibleTab1={setIsVisibleTab1Q3}
        >
            <div className="tab">
                <WeatherTable
                    title="Aerolinea"
                    data={dataq3_aerolinea}
                    counter={"retraso_aerolinea"}
                />
                
            </div>
            <div>
            <WeatherTable
                    title="Clima"
                    data={dataq3_clima}
                    counter={"retraso_clima"}
                />
            </div>
        </Tabs>
        </div>
        <div className="cards">
            <PieChart
                data={isVisibleTab1Q3 === 0 ? dataq3_aerolinea.map((item) => item.retraso_aerolinea).slice(0,5) : dataq3_clima.map((item) => item.retraso_clima).slice(0,5)}
                labels={isVisibleTab1Q3 === 0 ? dataq3_aerolinea.map((item) => item.aerolinea).slice(0,5) : dataq3_clima.map((item) => item.aerolinea).slice(0,5)}
            />
        </div>
        &nbsp;
    </div>
  );
};
