import React from 'react'
import "./mantenimiento.module.css";
import { Preloader } from '../preloader/Preloader';

export const Background = ({preloader, setPreloader,children}) => {

    return (
        <div className="contenedor">
          {children}
          <Preloader 
            preloader={preloader}
            setPreloader={setPreloader}
          />
          

        </div>
          
    );
}
